*{
    padding: 0;
    margin: 0;
    box-sizing: border-box;
    font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
}
.box{
    margin-left:auto;
    margin-right:auto;
    text-align: center;
    justify-content: center;
    margin-top: 20vh;
    margin-bottom: 30vh;
    background-color: rgb(255, 255, 255);
    width: 38%;
    border: 4px solid black;
    border-radius: 20px;
}
.main{
    width: 100%;
    height: 100vh;
    background-color: #3afcab;
    display: flex;
    justify-content: center;
    align-items: center;
}
.btnd{
    width: 100%;
    display: flex;
    justify-content: space-around;
    align-items: center;
    border-radius: 20px;
    margin-bottom: 10%;
}
.btn1:hover{
    color: #24ff02;
}
.btn2:hover{
    color: rgb(255, 5, 5);
}
.h11{
    margin-top: 10%;
    margin-bottom: 20%;
    font-size: 3vw;
    color: white;
    background-color:rgb(30, 12, 149);

}
@media (max-width:768px){
    .box{
      width:50%
    }
}